import Link from "next/link";
import { Shirt, MapPin, Phone, Mail, Clock, Instagram, Facebook } from "lucide-react";
import Image from "next/image";

export function Footer() {
  return (
    <footer className="bg-primary text-white border-t py-12">
      <div className="container grid gap-8 md:grid-cols-3">
        <div className="space-y-4 flex flex-col items-center md:items-start">
          <div className="flex items-center gap-2">
            <Image 
              src="/logo_blitz.png" 
              alt="Blitz Lavanderia" 
              width={160}
              height={50}
              className="object-contain bg-white p-2 rounded-lg"
            />
          </div>
          <p className="text-white/80 max-w-xs text-center md:text-left">
            Serviços de lavanderia profissional com qualidade e rapidez. Atendemos roupas, uniformes, edredons e muito mais.
          </p>
          <div className="flex gap-4">
            <Link href="https://instagram.com" className="text-white/70 hover:text-white transition-colors">
              <Instagram className="h-5 w-5" />
              <span className="sr-only">Instagram</span>
            </Link>
            <Link href="https://facebook.com" className="text-white/70 hover:text-white transition-colors">
              <Facebook className="h-5 w-5" />
              <span className="sr-only">Facebook</span>
            </Link>
          </div>
        </div>
        
        <div className="space-y-4 flex flex-col items-center md:items-start">
          <h4 className="font-semibold text-lg">Contato</h4>
          <ul className="space-y-3">
            <li className="flex items-start gap-2 text-sm text-white/80">
              <MapPin className="h-4 w-4 mt-0.5 flex-shrink-0" />
              <span>Rua Exemplo, 123 - Centro, Cidade - Estado</span>
            </li>
            <li className="flex items-center gap-2 text-sm text-white/80">
              <Phone className="h-4 w-4 flex-shrink-0" />
              <span>(11) 9999-9999</span>
            </li>
            <li className="flex items-center gap-2 text-sm text-white/80">
              <Mail className="h-4 w-4 flex-shrink-0" />
              <span>contato@blitzlavanderia.com.br</span>
            </li>
            <li className="flex items-start gap-2 text-sm text-white/80">
              <Clock className="h-4 w-4 mt-0.5 flex-shrink-0" />
              <span>Segunda a Sexta: 08h às 19h<br />Sábado: 08h às 13h</span>
            </li>
          </ul>
        </div>
        
        <div className="space-y-4 flex flex-col items-center md:items-start">
          <h4 className="font-semibold text-lg">Links Rápidos</h4>
          <nav className="grid gap-2">
            <Link href="/" className="text-sm text-white/80 hover:text-white">Home</Link>
            <Link href="/servicos" className="text-sm text-white/80 hover:text-white">Serviços</Link>
            <Link href="/sobre" className="text-sm text-white/80 hover:text-white">Sobre Nós</Link>
            <Link href="/contato" className="text-sm text-white/80 hover:text-white">Contato</Link>
          </nav>
        </div>
      </div>
      
      <div className="container mt-8 pt-8 border-t border-white/20">
        <div className="flex flex-col md:flex-row justify-between items-center gap-4">
          <p className="text-sm text-white/70">
            &copy; {new Date().getFullYear()} Blitz Lavanderia. Todos os direitos reservados.
          </p>
          <div className="flex gap-4">
            <Link href="/privacidade" className="text-xs text-white/70 hover:text-white transition-colors">
              Política de Privacidade
            </Link>
            <Link href="/termos" className="text-xs text-white/70 hover:text-white transition-colors">
              Termos de Serviço
            </Link>
          </div>
        </div>
      </div>
    </footer>
  );
} 